import React from 'react';
import { Row, Col, Card, Form, Select, Input, message, DatePicker, Button, Tag, Tabs, Image } from 'antd';
import axios from 'axios';
import { connect } from 'react-redux';
import moment from 'moment';
import { DownloadOutlined } from '@ant-design/icons';
import { download_client_attachment, download_work_attachment } from 'api';

import { order_attachment, work_attachment } from 'api';

import Loading from 'components/shared-components/Loading';

let { Option } = Select;
let { TabPane } = Tabs;

const EditOrderForm = (props) => {

    let { match, user_id, history, role_id, location } = props;
    let [data, setData] = React.useState(null);
    let [oldAttachments, setOldAttachments] = React.useState([]);
    let [newAttachments, setNewAttachments] = React.useState([]);
    let [oldWorkAttachment, setOldWorkAttachment] = React.useState([]);
    let [newWorkAttachment, setNewWorkAttachment] = React.useState([]);
    let [designer, setDesigner] = React.useState([]);
    let { order_id, order_token } = match.params;
    let [enableAssign, setEnableAssign] = React.useState(true);
    let [workerNote, setWorkerNote] = React.useState('');

    const getOrder = async () => {
        try {
            let res = await axios.post('/orderdetails', { user_id, order_id });
            setData(res.data.data);
            setOldAttachments(res.data.attachmentdata);
            setOldWorkAttachment(res.data.attachmentdataworker);
            // getDigitizerDesigner(res.data.data.order_designtype);
            if (res.data.data.order_designtype === 'Vector') {
                setEnableAssign(true)
            } else {
                setEnableAssign(false)
            }
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    // const getDigitizerDesigner = async (type) => {
    //     if (type === 'Vector') {
    //         let res = await axios.post('/designerlist', { user_id });
    //         setDesigner(res.data.data);
    //     } else {
    //         let res = await axios.post('/digitizerlist', { user_id });
    //         setDesigner(res.data.data);
    //     }
    // }

    const removeAttachment = async (attachment_id) => {
        try {
            let arr = oldAttachments.filter(item => item.attachment_id !== attachment_id);
            await axios.post('/deleteattachment', { user_id, attachment_id, order_id });
            setOldAttachments(arr);
        } catch (error) {
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const onChange = (changedFields, allFields) => {
        if (changedFields.designType === 'Vector') {
            setEnableAssign(true); //disable the dropdown
        } else if (changedFields.designType === 'Digitize') {
            setEnableAssign(false); //enable the dropdown
        }

        if (changedFields.order_workernote || changedFields.order_clienteditnote) {
            //set note for both worker and client edit
            setWorkerNote(changedFields.order_workernote || changedFields.order_clienteditnote);
        }
    }

    const openAttachment = (url) => {
        let a = document.createElement("a");
        a.href = url;
        a.download = url;
        a.target = '_blank';
        a.click();
        a.remove();
    }

    const editOrder = async (values) => {
        let removeMessage = message.loading('Updating Order', 0);
        try {
            let formData = new FormData();
            if (role_id == '3') {
                formData.append('orderstatus_id', '4');
                // formData.append('order_status', 'Assigned');
            }
            if (data.order_status === 'Back To Agent') {
                formData.append('order_status', 'Agent Fixed');
            }
            formData.append(`assign_to`, values.assign_to);
            formData.append('user_id', user_id);
            formData.append('order_id', order_id);
            formData.append('order_token', order_token);
            formData.append('client_id', data.client_id);
            formData.append('attachment_token', data.order_attachmenttoken);
            formData.append(`designName`, values.designName);
            formData.append(`deadlineDate`, values.deadlineDate.format('YYYY-MM-DD'));
            formData.append(`amountQuoted`, values.amountQuoted);
            formData.append(`designType`, values.designType);
            formData.append(`placement`, values.placement);
            formData.append(`managerDescription`, values.managerDescription);
            formData.append(`agentDescription`, values.agentDescription);
            formData.append(`poNumber`, values.poNumber);
            formData.append(`requiredFormat`, values.requiredFormat);
            formData.append(`level`, values.level);
            formData.append(`fabric`, values.fabric);
            formData.append(`noofcolors`, values.noofcolors);
            formData.append(`backgroundFill`, values.backgroundFill);
            formData.append(`height`, values.height);
            formData.append(`width`, values.width);
            formData.append(`noOfStitches`, values.noOfStitches);
            formData.append(`instructions`, values.instructions);
            formData.append(`colorblending`, values.colorblending);
            for (let j = 0; j < newAttachments.length; j++) {
                formData.append(`attachment[${j}]`, newAttachments[j]);
            }

            let res = await axios.post('/updateorder', formData);
            removeMessage();
            message.success(res.data.message);
            history.goBack();
        } catch (error) {
            console.log(error);
            removeMessage();
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const getFile = (e) => {
        //converting object into array
        if (role_id == 5 || role_id == 6) {
            //submiting work digitizer/designer
            let filesObj = e.target.files;
            let arr = []
            Object.keys(filesObj).map(key => {
                arr.push(filesObj[key]);
            });
            setNewWorkAttachment(arr);

        } else {
            //editing order
            let filesObj = e.target.files;
            let arr = []
            Object.keys(filesObj).map(key => {
                arr.push(filesObj[key]);
            });
            setNewAttachments(arr);
        }
    }

    const submitWork = async () => {
        let updateMessage = message.loading('Submitting Work');
        try {
            let formData = new FormData();
            formData.append('user_id', user_id);
            formData.append('type', 'attachment');
            formData.append('order_id', data.order_id);
            formData.append('orderstatus_id', '5');
            formData.append('order_token', data.order_token);
            formData.append('client_id', data.client_id);
            formData.append('attachment_token', data.order_attachmenttoken);
            for (let j = 0; j < newWorkAttachment.length; j++) {
                formData.append(`attachment[${j}]`, newWorkAttachment[j]);
            }
            if (data.order_status === 'Edit By Client') {
                formData.append('order_status', 'Edit By Client');
            }
            let res = await axios.post('/updateorderstatusworker', formData);
            history.goBack();
            updateMessage();
            message.success(res.data.message);
        } catch (error) {
            console.log(error);
            updateMessage();
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const backToAgent = async () => {
        try {
            let res = await axios.post('/updateorderstatutoedit', { user_id, order_id: data.order_id, order_status: 'Back To Agent' })
            history.goBack();
        } catch (error) {
            console.log(error);
        }
    }

    const backToManager = async () => {
        try {
            if (workerNote) {
                let res = await axios.post('/updateorderstatutoedit', {
                    user_id,
                    order_id: data.order_id,
                    order_status: 'Back To Manager',
                    order_workernote: workerNote
                });
                history.goBack();
            } else {
                message.error('Please enter a Note')
            }
        } catch (error) {
            console.log(error);
        }
    }

    const editByClient = async () => {
        try {
            let res = await axios.post('/updateorderstatutoedit', {
                user_id,
                order_id: data.order_id,
                order_status: 'Edit By Client',
                order_clienteditnote: workerNote,
            });
            history.goBack();
        } catch (error) {
            console.log(error);
        }
    }

    //assign edit order to designer
    const editAssign = async () => {
        try {
            let res = await axios.post('/updateorderstatutoedit', {
                user_id,
                order_id: data.order_id,
                order_status: 'Assign Edit',
            });
            history.goBack();
        } catch (error) {
            console.log(error);
        }
    }

    const dowloadFile = async (type) => {
        try {
            let a = document.createElement('a');
            a.download = 'attachment.zip';
            if (type === 'client') a.href = download_client_attachment + data.order_attachmenttoken;
            else a.href = download_work_attachment + data.order_attachmenttoken;
            a.click();
            a.remove();
        } catch (error) {
            console.log(error);
        }
    }

    React.useEffect(() => {
        getOrder();
    }, [])

    if (data === null) {
        return <Loading cover='content' />
    }
    console.log(typeof data.order_deadlinedate)
    return (
        <>
            <Card>
                <h3>Edit Order</h3>
            </Card>
            <Card>
                <Tabs defaultActiveKey="1">
                    <TabPane tab="Details" key="1">
                        <Form
                            name="basicInformation"
                            layout="horizontal"
                            onFinish={(role_id == 5 || role_id == 6) ? submitWork : editOrder}
                            onValuesChange={onChange}
                        >
                            <Row style={{ justifyContent: 'center' }}>
                                <Col xs={24} sm={24} md={24} lg={20} >
                                    <Row gutter={16} align="center">
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Deadline"
                                                name="deadlineDate"
                                                initialValue={moment(data.order_deadlinedate)}
                                            >
                                                <DatePicker
                                                    disabled={role_id == 5 || role_id == 6}
                                                    disabledDate={current => current && current <= moment().subtract(1, 'days')}
                                                />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Design Name"
                                                name="designName"
                                                initialValue='test'
                                                initialValue={data.order_designname}
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Amount Quoted"
                                                name="amountQuoted"
                                                initialValue='10'
                                                initialValue={data.order_amountquoted}
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Please enter a number!',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Design Type"
                                                name="designType"
                                                initialValue={'Vector'}
                                                initialValue={data.order_designtype}
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Select disabled={role_id == 5 || role_id == 6} >
                                                    <Option value={'Vector'}>
                                                        Vector
                                        </Option>
                                                    <Option value={'Digitize'}>
                                                        Digitize
                                        </Option>
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        {
                                            role_id == '3' &&
                                            <Col xs={24} sm={24} md={8}>
                                                <Form.Item
                                                    label="Assign To"
                                                    name="assign_to"
                                                    initialValue={data.assign || '-1'}
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'wrong entry',
                                                        },
                                                    ]}
                                                >
                                                    <Select disabled={enableAssign}>
                                                        <Option value={'-1'}>
                                                            Inhouse
                                            </Option>
                                                        <Option value={'1'}>
                                                            Outsource
                                            </Option>
                                                        {/* {
                                                    designer.map((item, index) => {
                                                        return (
                                                            <Option value={item.user_id} key={index}>
                                                                {item.user_name}
                                                            </Option>
                                                        )
                                                    })
                                                } */}
                                                    </Select>
                                                </Form.Item>
                                            </Col>
                                        }
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Placement"
                                                name="placement"
                                                initialValue={data.order_placement}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Manager Description"
                                                name="managerDescription"
                                                initialValue={data.order_managerdescription}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Agent Description"
                                                name="agentDescription"
                                                initialValue={data.order_agentdescription}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="PO Number"
                                                name="poNumber"
                                                initialValue={data.order_ponumber}
                                                rules={[
                                                    {
                                                        message: 'Please enter a number!',
                                                    },
                                                ]}
                                            >
                                                <Input type="number" disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Required Format"
                                                name="requiredFormat"
                                                initialValue={data.order_requiredformat}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Level"
                                                name="level"
                                                initialValue={data.order_level}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Fabric"
                                                name="fabric"
                                                initialValue={data.order_fabric}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="No. Of Colors"
                                                name="noofcolors"
                                                initialValue={data.order_noofcolors}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Color Blending"
                                                name="colorblending"
                                                initialValue={data.order_colorblending}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Form.Item
                                                label="Background Fill"
                                                name="backgroundFill"
                                                initialValue={data.order_backgroundfill}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} >
                                            <Form.Item
                                                label="Height"
                                                name="height"
                                                initialValue={data.order_height}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} >
                                            <Form.Item
                                                label="Width"
                                                name="width"
                                                initialValue={data.order_width}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} >
                                            <Form.Item
                                                label="No Of Stitches"
                                                name="noOfStitches"
                                                initialValue={data.order_noofstitches}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={24} >
                                            <Form.Item
                                                label="Instructions"
                                                name="instructions"
                                                initialValue={data.order_instructions}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 5 || role_id == 6} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={24}>
                                            <Form.Item
                                                label="Worker Note"
                                                name="order_workernote"
                                                initialValue={data.order_workernote}
                                                rules={[
                                                    {
                                                        message: 'wrong entry',
                                                    },
                                                ]}
                                            >
                                                <Input disabled={role_id == 3 || role_id == 4} />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={24}>
                                            <Form.Item
                                                label="Client Edit Note"
                                                name="order_clienteditnote"
                                                initialValue={data.order_clienteditnote}
                                            >
                                                <Input disabled={user_id != data.created_by || data.order_status !== 'Completed'} />
                                            </Form.Item>
                                        </Col>
                                        <Input type="file" multiple onChange={getFile} />
                                    </Row>
                                </Col>
                            </Row>
                            <Row justify="end">
                                {
                                    data.orderstatus_id < 8 &&
                                    <>

                                        {
                                            (user_id != data.created_by
                                                && role_id != 5
                                                && role_id != 6
                                                && data.order_status !== 'Edit By Client') &&
                                            <Button danger style={{ marginTop: 10, marginRight: 20, }} onClick={backToAgent}>
                                                Back To Agent
                                    </Button>
                                        }
                                        {
                                            ((role_id == 5 || role_id == 6) && data.order_status === 'In Progress') &&
                                            <Button danger style={{ marginTop: 10, marginRight: 20, }} onClick={backToManager}>
                                                Back To Manager
                                     </Button>
                                        }
                                        {
                                            (data.order_status === 'Completed' && data.created_by === user_id) &&
                                            <Button
                                                danger
                                                style={{ marginTop: 10, marginRight: 20, }}
                                                onClick={editByClient}
                                            >
                                                Edit by Client
                                    </Button>
                                        }
                                        {
                                            (data.order_status === 'Edit By Client' && role_id == 3) &&
                                            <Button
                                                danger
                                                style={{ marginTop: 10, marginRight: 20, }}
                                                onClick={editAssign}
                                            >
                                                Assign Edit
                                    </Button>
                                        }
                                        <Button type="primary" htmlType="submit" style={{ marginTop: 10, marginRight: 20, }}>
                                            {
                                                (role_id == 5 || role_id == 6) ?
                                                    'Submit Work'
                                                    :
                                                    'Save Changes'
                                            }
                                        </Button>
                                    </>
                                }
                            </Row>
                        </Form>
                    </TabPane>
                    <TabPane tab="Attachments" key="2">
                        <h3>
                            Client Files:
                            <Button type="link" onClick={() => dowloadFile('client')}>Download</Button>
                        </h3>
                        <div style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap' }}>
                            {
                                oldAttachments.map((item, index) => {
                                    return (
                                        <div
                                            style={{ display: 'flex', flexDirection: 'column', marginTop: 20 }}
                                            key={index}
                                        >
                                            <Image
                                                width={200}
                                                height={100}
                                                src={order_attachment + data.order_attachmenttoken + '/' + item.attachment_name}
                                                className="ml-5"
                                            />
                                        </div>
                                    )
                                })
                            }
                        </div>
                        <h3 style={{ marginTop: 30 }}>
                            Design Files:
                            {
                                oldWorkAttachment.length > 0 &&
                                <Button type="link" onClick={() => dowloadFile('worker')} > Download</Button>
                            }
                        </h3>
                        <div style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap', marginTop: 10 }}>
                            {
                                oldWorkAttachment.map((item, index) => {
                                    return (
                                        <div
                                            style={{ display: 'flex', flexDirection: 'column', marginTop: 20 }}
                                            key={index}
                                        >
                                            <Image
                                                width={200}
                                                height={100}
                                                src={work_attachment + data.order_attachmenttoken + '/' + item.attachment_name}
                                                className="ml-5"
                                            />
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </TabPane>
                </Tabs>

            </Card>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
});

export default connect(mapStateToProps)(EditOrderForm);